const FlyoutNav = () => {
    const [scrolled, setScrolled] = useState(false);
    const { scrollY } = useScroll();

    useMotionValueEvent(scrollY, "change", (latest) => {
        setScrolled(latest > 250 ? true : false);
    });

    return (
        <nav
            className={`fixed top-0 z-50 w-full px-6 pt-4 text-white
      transition-all duration-300 ease-out lg:px-12`}
        >
            {!scrolled && (

            <div className="mx-auto flex max-w-7xl items-center justify-between">
                <Logo />
                <div className="hidden gap-6 lg:flex">
                    <Links />
                    <CTAs />
                </div>
                <MobileMenu />
            </div>
            )}
        </nav>
    );
};

const Logo = ({ color = "white" }: { color?: string }) => {
    // Temp logo from https://logoipsum.com/
    const { darkMode } = useDeviceContext()

    return (
        <div className="flex items-center gap-2">
            {darkMode && color === "white" && (
                <Image src={AnataLABLogoWhite} alt="AnataLAB Logo" width={80} />
            )}
            {!darkMode && color === "white" && (
                <Image src={AnataLABLogoBlack} alt="AnataLAB Logo" width={80} />
            )}
            {darkMode && color === "black" && (
                <Image src={AnataLABLogoWhite} alt="AnataLAB Logo" width={80} />
            )}
            {!darkMode && color === "black" && (
                <Image src={AnataLABLogoWhite} alt="AnataLAB Logo" width={80} />
            )}
        </div>
    );
};

